import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { d, formatNumberWithDown } from '@cetus/utils'
import { Box, HStack, Popover, PopoverBody, PopoverContent, PopoverTrigger, Text } from '@chakra-ui/react'
import { useEffect, useMemo, useRef, useState } from 'react'

type UnstableRangeProps = {
  displayTokenASymbol: string
  displayTokenBSymbol: string
  minPrice?: string
  maxPrice?: string
  currPrice?: string
}
function UnstableRange(props: UnstableRangeProps) {
  const { displayTokenASymbol, displayTokenBSymbol, minPrice, maxPrice, currPrice } = props
  const [elementWidth, setElementWidth] = useState<number>(0)
  const [currPriceWidth, setCurrPriceWidth] = useState<number>(0)
  const elementRef = useRef<HTMLDivElement>(null)
  const currPriceRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const getElementWidth = () => {
      if (elementRef.current) {
        setElementWidth(elementRef.current.offsetWidth)
      }
    }
    const getCurrPriceWidth = () => {
      if (currPriceRef.current) {
        setCurrPriceWidth(currPriceRef.current.offsetWidth)
      }
    }

    getElementWidth()
    getCurrPriceWidth()

    const resizeObserver = new ResizeObserver(getElementWidth)
    const priceResizeObserver = new ResizeObserver(getCurrPriceWidth)

    if (elementRef.current) {
      resizeObserver.observe(elementRef.current)
    }
    if (currPriceRef.current) {
      priceResizeObserver.observe(currPriceRef.current)
    }

    return () => {
      if (elementRef.current) {
        resizeObserver.unobserve(elementRef.current)
      }
      if (currPriceRef.current) {
        priceResizeObserver.unobserve(currPriceRef.current)
      }
    }
  }, [])

  const sliderML = useMemo(() => {
    if (maxPrice === 'âˆž') return '0px'
    if (currPrice && minPrice && maxPrice) {
      if (d(currPrice).lessThanOrEqualTo(maxPrice) && d(currPrice).greaterThanOrEqualTo(minPrice)) {
        const relativePosition = d(currPrice)
          .sub(minPrice)
          .div(d(maxPrice).sub(d(minPrice)))
          .toString()
        return `${d(elementWidth).mul(relativePosition).toFixed(0)}px`
      }
      if (d(currPrice).lt(minPrice)) {
        return '-36px'
      }
      if (d(currPrice).gt(maxPrice)) {
        return 'calc(100% + 36px)'
      }
    }
    return '0px'
  }, [currPrice, minPrice, maxPrice, elementWidth])

  const priceLeft = useMemo(() => {
    const left = sliderML.replace('px', '')
    return Number(left) - currPriceWidth / 2 + 'px'
  }, [sliderML, currPriceWidth])

  const { isApp } = useWindowWidth()
  return (
    // <>
    //   {minPrice !== 'undefined' && maxPrice !== 'undefined' ? (
    //     <Flex marginTop="4px" background="primary_opacity.10" borderRadius="8px" p="12px">
    //       <Text color="primary_gray" fontSize="12px">
    //         Range
    //       </Text>
    //       <Text color="text_caption" fontSize="12px" marginLeft="4px">
    //         {formatNumber(minPrice, 6, true, Decimal.ROUND_DOWN)}-{formatNumber(maxPrice, 6, true, Decimal.ROUND_DOWN)} {displayTokenASymbol} per{' '}
    //         {displayTokenBSymbol}
    //       </Text>
    //     </Flex>
    //   ) : null}
    // </>
    <>
      {minPrice !== 'undefined' && maxPrice !== 'undefined' ? (
        <HStack w="100%" height="60px" background="primary_opacity.10" borderRadius="16px" p="0px 0px 8px">
          <Box
            w="100%"
            padding={minPrice && d(currPrice).lt(minPrice) ? '0 40px 0 60px' : maxPrice && d(currPrice).gt(maxPrice) ? '0 60px 0 40px' : '0 40px'}
            position="relative"
          >
            <Text
              whiteSpace="nowrap"
              position="absolute"
              top="20px"
              left={minPrice && d(currPrice).lt(minPrice) ? '60px' : '40px'}
              fontSize="12px"
              color="text_caption"
            >
              {formatNumberWithDown(minPrice)}
            </Text>
            <Box
              w="100%"
              position="relative"
              height="14px"
              ref={elementRef}
              bgImage="url('/images/img_range@2x.png')"
              bgSize="100% 14px"
              bgPosition="center"
            >
              <Popover isLazy placement="top" trigger={isApp ? 'click' : 'hover'} autoFocus={false} returnFocusOnClose={false} gutter={4}>
                <PopoverTrigger>
                  <Box as="button">
                    <Box
                      bg="#fff"
                      borderRadius="8px"
                      w="4px"
                      height="15px"
                      position="absolute"
                      top="50%"
                      transform="translateY(-50%)"
                      left={sliderML}
                      ml="-2px"
                      zIndex="5"
                    />
                  </Box>
                </PopoverTrigger>
                <PopoverContent
                  w="fit-content"
                  bg="none"
                  border="none"
                  ref={currPriceRef}
                  position="absolute"
                  top="-32px !important"
                  left={priceLeft}
                  zIndex="5"
                >
                  <PopoverBody fontSize="12px" w="fit-content" bg="none" border="none">
                    <Text whiteSpace="nowrap" fontSize="12px" color="primary">
                      {formatNumberWithDown(currPrice)}
                    </Text>
                  </PopoverBody>
                </PopoverContent>
              </Popover>
            </Box>
            <Text
              whiteSpace="nowrap"
              position="absolute"
              top="20px"
              right={maxPrice && d(currPrice).gt(maxPrice) ? '60px' : '40px'}
              fontSize="12px"
              color="text_caption"
            >
              {formatNumberWithDown(maxPrice)}
            </Text>
          </Box>
        </HStack>
      ) : // <Flex marginTop="4px" background="primary_opacity.10" borderRadius="8px" p="12px">
      //   <Text color="primary_gray" fontSize="12px">
      //     Range
      //   </Text>
      //   <Text color="text_caption" fontSize="12px" marginLeft="4px">
      //     {formatNumber(minPrice, 6, true, Decimal.ROUND_DOWN)}-{formatNumber(maxPrice, 6, true, Decimal.ROUND_DOWN)} {displayTokenASymbol} per{' '}
      //     {displayTokenBSymbol}
      //   </Text>
      // </Flex>
      null}
    </>
  )
}
export default UnstableRange
