import useDisplayTickPrice from '@/hooks/common/useDisplayTickPrice'
import useGetVaultList from '@/hooks/vaults/useGetVaultList'
import useVaultsStore from '@/store/vaults/useVaultsList'
import { Block } from '@cetus/design'
import { Token } from '@cetus/types'
import { CoinPairImage, VTextLabelBox } from '@cetus/ui-kit'
import { isAvailableObject } from '@cetus/utils'
import { Box, Flex, Text, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import { VaultsAprTooltipContent } from '../list/common/VaultsAprBlock'
import VaultRange from './VaultRange'

type UnstableInfoProps = {
  displayTokenA?: Token
  displayTokenB?: Token
  feeDisplay: string
  clmmPool: string
  isReverse: boolean
  minPrice?: string
  maxPrice?: string
  isUnstableVault: boolean
  currPrice?: string
}
function UnstableInfo(props: UnstableInfoProps) {
  const { displayTokenA, displayTokenB, feeDisplay, clmmPool, isReverse, minPrice, maxPrice, isUnstableVault, currPrice } = props
  const { vaultStatsObj } = useVaultsStore()
  const { getVaultsStats } = useGetVaultList()
  const [vaultsAPY, setVaultsAPY] = useState('')
  useEffect(() => {
    if (isAvailableObject(vaultStatsObj) && vaultStatsObj[clmmPool]) {
      setVaultsAPY(vaultStatsObj[clmmPool]?.vaultsApyDisplay)
    } else {
      getVaultsStats()
    }
  }, [vaultStatsObj, clmmPool])

  const { displayMaxPrice, displayMinPrice } = useDisplayTickPrice({ minPrice, maxPrice, changeSide: isReverse })

  return (
    <VStack width="calc(100% + 32px)" padding="0 16px" alignItems="left" paddingBottom="16px" borderBottom="1px solid" borderColor="border">
      <Flex>
        <CoinPairImage
          coinAIconUrl={displayTokenA?.logo_url}
          coinBIconUrl={displayTokenB?.logo_url}
          imageStyle={{
            w: '32px',
            h: '32px'
          }}
          imgBoxStyle={{
            w: '32px',
            h: '32px'
          }}
        />
        <Flex marginLeft="8px" justifyContent="space-between" alignItems="center" width="100%">
          <Box>
            <Flex alignItems="center">
              <Text fontSize="16px" color="text_caption">
                {`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
              </Text>
              <Block w="unset" p="3px 12px 2px" borderRadius="12px" bg="none" borderColor="primary" marginLeft="8px">
                <Text fontSize="12px" color="primary">
                  {feeDisplay}
                </Text>
              </Block>
            </Flex>
          </Box>
          <Flex flexDirection="column">
            <VTextLabelBox
              wrapStyle={{
                gap: '2px',
                alignItems: 'flex-end !important'
              }}
              title="APY"
              titleStyle={{
                color: 'primary',
                fontSize: '12px'
              }}
              valueTooltip={
                <VaultsAprTooltipContent
                  vaultsLstApyDisplay={vaultStatsObj[clmmPool]?.vaultsLstApyDisplay || ''}
                  vaultsApyDisplay={vaultStatsObj[clmmPool]?.vaultsApyDisplay || ''}
                  vaultsTotalApyDisplay={vaultStatsObj[clmmPool]?.vaultsTotalApyDisplay || ''}
                  vaultsAprDisplay={vaultStatsObj[clmmPool]?.vaultsAprDisplay || ''}
                  category={vaultStatsObj[clmmPool]?.category}
                />
              }
              value={vaultsAPY || '-'}
              valueStyle={{
                color: 'text_highlight',
                h: '20px',
                lineHeight: '20px',
                fontSize: '14px',
                fontWeight: '500'
              }}
            />
          </Flex>
        </Flex>
      </Flex>
      <VaultRange
        displayTokenASymbol={isReverse ? displayTokenB?.symbol : displayTokenB?.symbol}
        displayTokenBSymbol={isReverse ? displayTokenA?.symbol : displayTokenA?.symbol}
        minPrice={displayMinPrice}
        maxPrice={displayMaxPrice}
        currPrice={currPrice}
      />
    </VStack>
  )
}

export default UnstableInfo
