import Slippage from '@/components/common/Slippage'
import { useGetSuiStakeProtocol } from '@/hooks/unstable-vault/useVaultsHelper'
import useVaultsAddStore from '@/store/unstable-vaults/useVaultsAdd'
import { SelectTab } from '@cetus/design'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import { useInterval } from '@cetus/hooks'
import { Token } from '@cetus/types'
import { RefreshButton } from '@cetus/ui-kit'
import { Flex, HStack, Text } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import ZapSwitch from './common/ZapSwitch'

const hoverCoinTips =
  'You can also choose to deposit in only one asset type. In that case,the vault will convert your asset to the required propositions using Cetus.Please note that trading fees & slippage will be applied. When slippage is involved,a certain amount of post-swap dust (no loss of funds) will be returned to you.'

const hoverSuiTips = (stakeProtocolName?: string, symbol?: string) => {
  return `When you deposit in only SUI tokens, a part of your SUI amount will be converted to ${symbol} via ${stakeProtocolName} liquid staking services to match required liquidity compositions of the liquidity pool on Cetus. A certain amount of dust (no loss of funds) will be returned to you if slippage is involved.`
}
type DepositWithdrawModeProps = {
  displayTokenA?: Token
  displayTokenB?: Token
  currentActionToken: string // å½“å‰æ“ä½œtoken
  setCurrentActionToken: (value: string) => void
  isCheckedZAP: boolean // æ˜¯å¦å¼€å¯zapæ¨¡å¼
  currentActionTab: string // å½“å‰æ“ä½œç±»åž‹ Deposit Withdraw
  setIsCheckedZAP: (value: boolean) => void
  setAssetAction: (value: string) => void
  setCurrentActionTab: (value: string) => void
  autoRefresh: () => void
  isUnstableVault: boolean
}

function DepositWithdrawMode(props: DepositWithdrawModeProps) {
  const {
    displayTokenA,
    displayTokenB,
    currentActionToken,
    setCurrentActionToken,
    isCheckedZAP,
    setIsCheckedZAP,
    currentActionTab,
    setAssetAction,
    setCurrentActionTab,
    isUnstableVault,
    autoRefresh
  } = props

  const { stakeProtocolName } = useGetSuiStakeProtocol(displayTokenA?.coin_type)
  const { setAmountInputA, setAmountInputB } = useVaultsAddStore()

  const depositTypeList: Tab[] = useMemo(() => {
    if (currentActionTab == 'Deposit') {
      const result = [
        {
          label: displayTokenA?.symbol + ' only',
          value: displayTokenA?.symbol + ' only',
          action: displayTokenA?.coin_type,
          afterIcon: isCheckedZAP && !isUnstableVault ? { xlinkHref: '#icon-icon_tips' } : undefined,
          tooltip: isUnstableVault ? `You can add liquidity in this pool with only ${displayTokenA?.symbol}.` : hoverCoinTips
        },
        {
          label: `${displayTokenA?.symbol}+${displayTokenB?.symbol}`,
          value: `${displayTokenA?.symbol}+${displayTokenB?.symbol}`,
          action: 'both',
          afterIcon: isCheckedZAP && !isUnstableVault ? { xlinkHref: '#icon-icon_tips' } : undefined,
          tooltip: isUnstableVault && isCheckedZAP ? `You can add any value you want from both assets.` : ''
        },
        {
          label: displayTokenB?.symbol + ' only',
          value: displayTokenB?.symbol + ' only',
          action: displayTokenB?.coin_type,
          afterIcon: isCheckedZAP && !isUnstableVault ? { xlinkHref: '#icon-icon_tips' } : undefined,
          tooltip: isUnstableVault
            ? `You can add liquidity in this pool with only ${displayTokenB?.symbol}.`
            : hoverSuiTips(stakeProtocolName, displayTokenA?.symbol)
        }
      ]
      return isUnstableVault ? result : result.filter(ele => ele.action !== 'both')
    } else {
      return [
        {
          label: displayTokenA?.symbol + ' only',
          value: displayTokenA?.symbol + ' only',
          action: displayTokenA?.coin_type
        },
        {
          label: displayTokenB?.symbol + ' only',
          value: displayTokenB?.symbol + ' only',
          action: displayTokenB?.coin_type
        }
      ]
    }
  }, [displayTokenA, displayTokenB, currentActionTab, isUnstableVault, isCheckedZAP, stakeProtocolName])

  const tabList: Tab[] = [
    {
      label: 'Deposit',
      value: 'Deposit'
    },
    {
      label: 'Withdraw',
      value: 'Withdraw'
    }
  ]
  const [refreshCount, setRefreshCount] = useState<number>(0)
  // 30sè½®è¯¢
  useInterval({
    interval: 1000,
    callback: () => {
      setRefreshCount(refreshCount + 1)
      if (refreshCount >= 20) {
        handleAutoRefresh()
      }
    }
  })

  const handleAutoRefresh = () => {
    setRefreshCount(0)
    autoRefresh()
  }

  const tooltipType = useMemo(() => {
    if (currentActionTab == 'Deposit') {
      return isUnstableVault ? 'customDeposit' : 'deposit'
    }
    return 'withdraw'
  }, [currentActionTab, isUnstableVault])
  return (
    <>
      <HStack width="100%" alignItems="center">
        <SelectTab
          type="borderTab"
          wrapStyle={{
            w: '100%',
            h: '54px',
            border: 'none',
            bg: 'none'
          }}
          itemStyle={{
            fontSize: '14px',
            mr: '28px',
            fontWeight: '500'
          }}
          tabList={tabList}
          currentTab={currentActionTab}
          handleChangeTab={(item: Tab) => {
            setCurrentActionTab(item.value)
            setAssetAction(isCheckedZAP ? displayTokenA?.coin_type : 'both')
            setCurrentActionToken(displayTokenA?.symbol + ' only')
          }}
        />
        <Slippage slippageType="vaults" compact={true} />
        <RefreshButton
          handleRefresh={handleAutoRefresh}
          w="28px"
          h="28px"
          minW="28px"
          minH="28px"
          innerStyle={{ w: '28px', h: '28px', borderRadius: '8px', bg: 'bg_secondary' }}
        />
      </HStack>
      <Flex width="100%" alignItems="center" justify="space-between" mt="8px">
        <Text>{currentActionTab} Amounts</Text>
        {/* <ZapSwitch
          isUnstableVault={isUnstableVault}
          displayTokenA={displayTokenA}
          currentActionTab={currentActionTab}
          isCheckedZAP={isCheckedZAP}
          setIsCheckedZAP={setIsCheckedZAP}
          setCurrentActionToken={setCurrentActionToken}
          setAssetAction={setAssetAction}
        /> */}
        <ZapSwitch
          isCheckedZAP={isCheckedZAP}
          zapText={currentActionTab == 'Deposit' ? 'Zap In' : 'Zap Out'}
          tooltipType={tooltipType}
          zapSwitchChange={status => {
            setAmountInputA('')
            setAmountInputB('')
            setIsCheckedZAP(!isCheckedZAP)
            if (!status) {
              setAssetAction('both')
            } else {
              setAssetAction(displayTokenA?.coin_type)
            }
            setCurrentActionToken(displayTokenA?.symbol + ' only')
          }}
        />
      </Flex>
      {isCheckedZAP ? (
        <SelectTab
          type="outlineTab"
          wrapStyle={{
            w: {
              base: '100%'
            },
            h: '42px',
            padding: '4px',
            marginTop: '8px'
            // marginBottom: '4px'
          }}
          itemStyle={{
            w: `${100 / depositTypeList.length}%`,
            fontSize: '14px',
            borderRadius: '8px'
          }}
          tabList={depositTypeList}
          currentTab={currentActionToken}
          handleChangeTab={item => {
            setCurrentActionToken(item.value)
            setAssetAction(item.action)
          }}
        />
      ) : null}
    </>
  )
}

export default DepositWithdrawMode
