import vaultInterface from '@/hooks/vault-interface'
import useGetApiVaultInfo from '@/hooks/vaults/useGetApiVaultInfo.ts'
import useVaultsDetailStore from '@/store/vaults/useVaultsDetail.ts'
import { AssetActionType } from '@/types'
import { useAccountBalance } from '@cetus/hooks'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { d } from '@cetus/utils'
import { Box, HStack, Modal, ModalBody, ModalCloseButton, ModalContent, ModalOverlay, Skeleton, Stack, Text, VStack } from '@chakra-ui/react'
import { lazy, Suspense, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import PoweredByHaedal from './common/PoweredByHaedal.tsx'
import DepositWithdrawMode from './DepositWithdrawMode.tsx'
import UnstableInfo from './VaultInfo.tsx'

const VaultDepositMode = lazy(() => import('./DepositMode.tsx'))
// const VaultDepositMode = lazy(() => new Promise((_, reject) => setTimeout(() => reject(new Error('Lazy load failed!')), 500000)))
const VaultWithdrawMode = lazy(() => import('./WithdrawMode.tsx'))

type VaultModalProps = {
  isOpen: boolean
  setIsOpen: (status: boolean) => void
  displayTokenA?: Token | undefined
  displayTokenB?: Token | undefined
  feeDisplay: string
  clmmPool: string
  vaultId: string
  isReverse: boolean
  isUnstableVault: boolean
}
function VaultModal(props: VaultModalProps) {
  const { isOpen, setIsOpen, displayTokenA, displayTokenB, feeDisplay, clmmPool, vaultId, isReverse, isUnstableVault } = props
  const [currentActionTab, setCurrentActionTab] = useState('Deposit')
  const [currentActionToken, setCurrentActionToken] = useState(displayTokenA?.symbol + ' only')
  const [assetAction, setAssetAction] = useState<AssetActionType>('both')
  const [isCheckedZAP, setIsCheckedZAP] = useState(false)
  const useGetContractVaultInfo = vaultInterface.useGetContractVaultInfo(isUnstableVault)

  const { apiVaultInfo, getApiVaultInfo } = useGetApiVaultInfo(vaultId)
  const {
    fetchVaultInfoFromContract,
    contractVault,
    lpTokenInfo,
    fetchClmmPoolFromContract,
    holdCoinAmounts,
    maxPrice,
    currPrice,
    minPrice,
    fetchVaultBalance,
    isBalanceLoading,
    vaultTvl,
    hardCapUSD,
    depositRatio,
    refreshPythPrice,
    calculateVaultTvlLoading,
    maxCapNum,
    quoteCoin,
    baseCoin,
    holdLpAmount,
    availableCapacityWithQuoteCoin,
    availableCapacityWithBaseCoin,
    gtAumLimit
  } = useGetContractVaultInfo(
    apiVaultInfo?.vaultsId,
    apiVaultInfo?.clmmPoolAddress,
    apiVaultInfo?.tokenA,
    apiVaultInfo?.tokenB,
    apiVaultInfo?.isReverse,
    true
  )

  const { fetchAccountBalance } = useAccountBalance()

  const navigate = useNavigate()
  const { currentAccount, isOpenWalletModal } = useAccountStore()
  const autoRefresh = () => {
    // åˆ·æ–°ä½™é¢
    fetchAccountBalance()

    console.log('ðŸš€ðŸš€ðŸš€ ~ index.tsx:57 ~ autoRefresh ~ apiVaultInfo:', apiVaultInfo)
    if (apiVaultInfo?.category == 'haedal') {
      fetchVaultBalance(currentAccount?.address, vaultId, apiVaultInfo?.tokenA?.decimals)
    }

    // åˆ·æ–° clmm åˆçº¦
    if (apiVaultInfo) {
      fetchClmmPoolFromContract(apiVaultInfo.clmmPoolAddress, true)
    }

    // åˆ·æ–°Vaults åˆçº¦
    if (vaultId) {
      fetchVaultInfoFromContract(vaultId, true)
      getApiVaultInfo(vaultId, true)
    }
  }

  useEffect(() => {
    setIsCheckedZAP(false)
    setAssetAction('both')
  }, [currentActionTab])

  useEffect(() => {
    if (apiVaultInfo) {
      fetchClmmPoolFromContract(apiVaultInfo.clmmPoolAddress, true)
    }
  }, [apiVaultInfo?.clmmPoolAddress])

  useEffect(() => {
    if (vaultId) {
      fetchVaultInfoFromContract(vaultId, true)
    }
  }, [vaultId])

  const { setVaultCoinAmounts } = useVaultsDetailStore()
  useEffect(() => {
    setVaultCoinAmounts({ coinAmountA: '0', coinAmountB: '0', displayCoinA: '0', displayCoinB: '0' })
  }, [])

  return (
    <Modal
      isCentered
      isOpen={isOpen && !isOpenWalletModal}
      onClose={() => {
        setIsOpen(false)
        setCurrentActionTab('Deposit')
      }}
    >
      <ModalOverlay />
      <ModalContent width={{ base: '100%', lg: '420px' }}>
        <ModalBody p="0 0 8px">
          <VStack width="100%">
            <VStack
              width="100%"
              p="6px 16px 16px"
              mb="-16px"
              gap="0"
              borderRadius="12px"
              sx={{
                backgroundImage: "url('/images/vault_modal_bg@2x.png')",
                backgroundPosition: 'top center',
                backgroundRepeat: 'no-repeat',
                backgroundSize: 'cover'
              }}
            >
              <HStack h="36px" w="100%" mb="12px">
                <Text color="text_caption" fontWeight="500" fontSize="16px">
                  Auto Vault
                </Text>
                <ModalCloseButton />
              </HStack>
              <UnstableInfo
                displayTokenA={displayTokenA}
                displayTokenB={displayTokenB}
                feeDisplay={feeDisplay}
                clmmPool={clmmPool}
                isReverse={isReverse}
                minPrice={minPrice}
                maxPrice={maxPrice}
                currPrice={isReverse ? d(1).div(d(currPrice)).toString() : currPrice}
                isUnstableVault={isUnstableVault}
              />
              <DepositWithdrawMode
                displayTokenA={displayTokenA}
                displayTokenB={displayTokenB}
                currentActionTab={currentActionTab}
                setCurrentActionTab={setCurrentActionTab}
                currentActionToken={currentActionToken}
                setCurrentActionToken={setCurrentActionToken}
                isCheckedZAP={isCheckedZAP}
                setIsCheckedZAP={setIsCheckedZAP}
                setAssetAction={setAssetAction}
                isUnstableVault={isUnstableVault}
                autoRefresh={autoRefresh}
              />
            </VStack>
            {/* <HStack bg="primary_opacity.10" borderRadius="12px" padding="8px 12px" lineHeight="16px" marginTop="12px">
              <Text color="primary" fontSize="12px">
                Automatically stake your LP token into the farm to enjoy higher yield.
              </Text>
            </HStack> */}

            <VStack w="100%" p="0 16px">
              <Suspense
                fallback={
                  <Stack w="100%" height="292px">
                    <VStack
                      align="flex-end"
                      w="100%"
                      h="108px"
                      p="16px"
                      borderRadius="16px"
                      border="1px solid"
                      borderColor="border"
                      bg="bg_secondary"
                    >
                      <Skeleton w="100%" height="20px" />
                      <Skeleton w="40%" height="20px" />
                    </VStack>
                    <VStack
                      align="flex-end"
                      w="100%"
                      h="108px"
                      p="16px"
                      borderRadius="16px"
                      border="1px solid"
                      borderColor="border"
                      bg="bg_secondary"
                    >
                      <Skeleton w="100%" height="20px" />
                      <Skeleton w="40%" height="20px" />
                    </VStack>
                    <Box w="100%" h="52px" mb="8px" borderRadius="12px">
                      <Skeleton w="100%" height="52px" borderRadius="12px" />
                    </Box>
                  </Stack>
                }
              >
                {currentActionTab == 'Deposit' && (
                  <VaultDepositMode
                    isUnstableVault={isUnstableVault}
                    displayTokenA={displayTokenA}
                    displayTokenB={displayTokenB}
                    vaultId={vaultId}
                    isReverse={isReverse}
                    assetAction={assetAction}
                    isCheckedZAP={isCheckedZAP}
                    currentActionTab={currentActionTab}
                    autoRefresh={autoRefresh}
                    apiVaultInfo={apiVaultInfo}
                    quoteCoin={quoteCoin}
                    lpTokenInfo={lpTokenInfo}
                    baseCoin={baseCoin}
                    maxCapNum={maxCapNum}
                    availableCapacityWithQuoteCoin={availableCapacityWithQuoteCoin}
                    availableCapacityWithBaseCoin={availableCapacityWithBaseCoin}
                    hardCapUSD={hardCapUSD}
                    vaultTvl={vaultTvl}
                    depositRatio={depositRatio}
                    calculateVaultTvlLoading={calculateVaultTvlLoading}
                    contractVault={contractVault}
                    refreshPythPrice={refreshPythPrice}
                    gtAumLimit={gtAumLimit}
                    key={vaultId}
                  />
                )}
              </Suspense>

              <Suspense
                fallback={
                  <Stack w="100%" height="292px">
                    <VStack
                      align="flex-end"
                      w="100%"
                      h="108px"
                      p="16px"
                      borderRadius="16px"
                      border="1px solid"
                      borderColor="border"
                      bg="bg_secondary"
                    >
                      <Skeleton w="100%" height="20px" />
                      <Skeleton w="60%" height="20px" />
                    </VStack>
                    <VStack
                      align="flex-end"
                      w="100%"
                      h="108px"
                      p="16px"
                      borderRadius="16px"
                      border="1px solid"
                      borderColor="border"
                      bg="bg_secondary"
                    >
                      <Skeleton w="100%" height="20px" />
                      <Skeleton w="40%" height="20px" />
                    </VStack>
                    <Box w="100%" h="52px" mb="8px" borderRadius="12px">
                      <Skeleton w="100%" height="52px" borderRadius="12px" />
                    </Box>
                  </Stack>
                }
              >
                {currentActionTab == 'Withdraw' && (
                  <VaultWithdrawMode
                    isUnstableVault={isUnstableVault}
                    displayTokenA={displayTokenA}
                    displayTokenB={displayTokenB}
                    vaultId={vaultId}
                    isReverse={isReverse}
                    assetAction={assetAction}
                    isCheckedZAP={isCheckedZAP}
                    autoRefresh={autoRefresh}
                    apiVaultInfo={apiVaultInfo}
                    lpTokenInfo={lpTokenInfo}
                    holdCoinAmounts={holdCoinAmounts}
                    fetchVaultBalance={fetchVaultBalance}
                    isBalanceLoading={isBalanceLoading}
                    holdLpAmount={holdLpAmount}
                  />
                )}
              </Suspense>

              {/* {isUnstableVault && ( */}
              <HStack
                borderTop="1px solid"
                borderColor="border"
                width="calc(100% + 32px)"
                padding="12px 16px 4px"
                justifyContent={isUnstableVault ? 'space-between' : 'center'}
              >
                <PoweredByHaedal mt="0" category={isUnstableVault ? 'Haedal' : 'Cetus'} />
                <Text
                  fontSize="12px"
                  _hover={{ color: 'primary' }}
                  whiteSpace="nowrap"
                  cursor="pointer"
                  onClick={() => navigate(`/vaults-detail/${vaultId}/${apiVaultInfo?.category}`)}
                >
                  Vault Details &gt;
                </Text>
              </HStack>
            </VStack>
            {/* )} */}
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
export default VaultModal
